.app-container {
  min-height: 100vh;
  padding-bottom: 40px;
  overflow-x: hidden;
  width: 100%;
  box-sizing: border-box;
}

.app-container--light {
  background: #FFD600;
  --text-color: #000000;
}

.app-container--dark {
  background: #181818;
  --text-color: white;
}

.schedule-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: clamp(20px, 4vw, 40px);
  width: 100%;
  max-width: 1800px;
  margin: 0 auto;
  position: relative;
  box-sizing: border-box;
}

.schedule-container--with-menu {
  /* Отступ под фиксированное меню + донат-кнопки */
  padding: 125px clamp(12px, 3vw, 24px) 0;
}

@media (min-width: 721px) {
  .schedule-container--with-menu {
    /* Когда меню и донаты уже могут становиться в два ряда (до 960) оставляем увеличенный отступ */
    padding: 125px clamp(12px, 3vw, 24px) 0;
  }
}

@media (min-width: 961px) {
  .schedule-container--with-menu {
    /* Меню в одну строку (961-1199) — компактный отступ */
    padding: 105px clamp(12px, 3vw, 24px) 0;
  }
}

@media (min-width: 1200px) {
  .schedule-container--with-menu {
    /* Ещё компактнее на больших экранах */
    padding: 105px clamp(12px, 3vw, 24px) 0;
  }
}

.schedule-container--without-menu {
  padding: clamp(20px, 4vh, 40px) clamp(12px, 3vw, 24px) 0;
}

.loading-message {
  font-size: clamp(24px, 4vw, 36px);
  font-weight: 700;
  text-align: center;
}

.loading-message--light {
  color: #000000;
}

.loading-message--dark {
  color: #fff;
}

.error-message {
  color: tomato;
  font-weight: 700;
  font-size: clamp(18px, 3vw, 24px);
  text-align: center;
}

.empty-message {
  font-size: clamp(18px, 3vw, 24px);
  text-align: center;
}

.empty-message--light {
  color: #000000;
}

.empty-message--dark {
  color: #fff;
}

.day-column {
  width: clamp(300px, 30vw, 400px);
  flex-shrink: 0;
  box-sizing: border-box;
}

/* На узких экранах, когда колонки складываются в столбец — делаем их на ~25% шире, но не шире экрана */
@media (max-width: 720px) {
  .schedule-container .day-column {
    width: clamp(360px, 40vw, 500px);
    max-width: 95vw;
  }
}


.day-rows-container {
  display: grid;
  grid-template-columns: clamp(80px, 10vw, 100px) 1fr;
  gap: 0 clamp(12px, 2.4vw, 18px);
  row-gap: clamp(20px, 3.9vw, 30px);
  align-items: start;
  margin-top: clamp(10px, 4vw, 20px);
}
