.schedule-row-wrapper {
  display: contents;
}

.schedule-row-wrapper > .content-container {
  max-width: 820px;
}

.schedule-row--light {
  color: #000000;
}

.schedule-row--dark {
  color: white;
}

.time-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 0;
  padding-right: 8px;
  width: clamp(80px, 10vw, 100px);
  min-width: clamp(80px, 10vw, 100px);
}

.time {
  font-weight: 700;
  font-size: clamp(32px, 4.125vw, 36px);
  text-align: left;
  line-height: 1;
  margin-top: 0;
  color: var(--text-color, white);
  white-space: nowrap;
  width: 100%;
}

.content-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-left: 8px;
  width: 100%;
}

.content-header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 12px;
  width: 100%;
}

.content-text {
  flex: 1;
  min-width: 0;
}

.championship {
  font-weight: 700;
  font-size: clamp(20px, 2.55vw, 24px);
  line-height: 1;
}

.stage {
  font-weight: 700;
  font-size: clamp(20px, 2.55vw, 24px);
  line-height: 1;
}

.place-session {
  font-weight: 600;
  font-size: clamp(16px, 1.575vw, 18px);
  margin-top: 8px;
}

.commentators-container {
  margin-top: 12px;
}

.calendar-buttons {
  display: flex;
  flex-direction: column;
  gap: 4px;
  align-items: center;
  flex-shrink: 0;
  margin-left: auto;
}

.calendar-button {
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 4px 2px;
  opacity: 0.7;
  transition: opacity 0.2s, transform 0.2s;
  flex-shrink: 0;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: clamp(31px, 3.8vw, 35px);
  height: clamp(31px, 3.8vw, 35px);
  align-self: flex-start;
}

.calendar-button:hover {
  opacity: 1;
  transform: scale(1.1);
}

.calendar-button:active {
  transform: scale(0.95);
}

.calendar-icon {
  width: 100%;
  height: 100%;
}

/* Светлая тема: черный календарь с желтыми деталями */
.calendar-button--light .calendar-icon-outline {
  stroke: #000000;
  fill: none;
}

.calendar-button--light .calendar-icon-accent {
  fill: #FFD600;
}

.calendar-button--light .calendar-icon-letter {
  fill: #000000;
}

/* Темная тема: желтый календарь с черными деталями */
.calendar-button--dark .calendar-icon-outline {
  stroke: #FFD600;
  fill: none;
}

.calendar-button--dark .calendar-icon-accent {
  fill: #000000;
}

.calendar-button--dark .calendar-icon-letter {
  fill: #FFD600;
}

